*
{
    margin: 0;
    padding: 0;
}
body{
    background-color: orchid;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 200px;
    font-size: 30px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;

}
ul{
    list-style-type: none;
    padding: 0;
}
.menu{
    padding: 10px;
    color: black;
    border-radius: 5px;
}
button{
    margin-left: 10px;
    padding: 10px;
    background-color: green;
    counter-reset: black;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}
button:hover{
    background-color: rgb(9, 246, 9);
}